body {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
}
.logo {
  width: 50%;
  height: auto;
  margin: 1rem auto;
  display: block;
}
.box {
  border: 1px solid #000;
  margin-bottom: 1rem;
  margin-left: 1rem;
  padding: 1rem;
}
.btn-container {
  align-items: center;
  justify-content: center;
  position: relative;
}
.btn-detail {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.detail-list {
  width: 50%;
  height: auto;
  margin: 1rem auto;
  border: 1px solid #000;
  list-style: none;
}
.detail-list li {
  width: 100%;
  text-align: center;
  margin: 0.5rem 0;
  text-transform: capitalize;
}
